/* Import file */

@import "mixin";

/* Specification */

html {
  -webkit-box-sizing: border-box;
     -moz-box-sizing: border-box;
          box-sizing: border-box;
}

body {
  padding: 28px 0 0 0;
  margin: 0;
  margin: 0 auto;
  font-family: 'PT Sans', sans-serif;
}

*, *:before, *:after {
  -webkit-box-sizing: inherit;
  -moz-box-sizing: inherit;
  box-sizing: inherit;
}

ul, li, p {
  display: block;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  letter-spacing: 0.02em;
}

h1, h2, h3, h4, h5, h6 {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

div, section, footer, header {
  box-sizing: border-box;
  position: relative;
}

button, input, textarea {
  outline: none !important;
}

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  display: none;
}

a, a:hover {text-decoration: none;}

.upper {text-transform: uppercase;}

.menuToggle {display: none;}

/* Content */
@include respond-until(1920px) {
  .container {
    width: 1215px;
    margin: 0 auto;
  }

  .container-fluide {
    margin: 0 auto;
    width: 1350px;
    padding: 0;
  }

  .head {
    display: grid;
    grid-template-rows: auto auto;
    grid-template-columns: 100%;
    &__top {
      display: grid;
      grid-template-columns: 246px auto 30%;
    }

    &__logo {
      img {width: 100%;}
      margin-top: 10px;
      &-descripton {
        font-size: 14px;
        color: #747688;
      }
    }

    &__search {
      margin-left: 45px;
      width: 495px;
      &-in {
        width: 100%;
        padding: 0em 1em;
        padding-right: 5em;
        position: relative;
        top: 0;
        border: none;
        border: 1px solid #b9b8bd;
        @include border-radius(3px);
        line-height: 45px;
      }

      &-btn {
        border: none;
        background: url("../img/search-gold.png") no-repeat center center;
        width: 20px;
        height: 20px;
        position: absolute;
        right: 1em;
        top: 1em;
        z-index: 1;
        padding: 0;
        cursor: pointer;
        &:focus {background: url("../img/search-standart.png") no-repeat center center;}
      }
    }
    &__contact {
      display: flex;
      -webkit-justify-content: flex-end;
              justify-content: flex-end;
      padding-top: 5px;
      a {
        line-height: 40px;
        font-size: 23px;
        color: #181616;
        margin-right: 16px;
      }
      &-wrap {
        font-size: 14px;
        margin-top: 3px;
      }
      &-time {color: #D5A970;}
      &-delivery {color: #2B3056;}
    }

    &__navigate {
      padding-top: 31px;
      display: grid;
      grid-template-columns: auto 340px;
    }

    &__menu {
      &-icon {
        display: inline-block;
        width: 18px;
        height: 13px;
        margin-right: 8px;
        background: url("../img/menu-gold.png") no-repeat left center;
      }
      &-list {
        margin-right: 50px;
        display: flex;
        -webkit-justify-content: space-between;
                justify-content: space-between;
      }
      &-item {
        font-weight: bold;
        font-size: 16px;
        text-transform: uppercase;
        a {color: #171717;}
        .dropdownArrow {
          position: absolute;
          top: 1px;
          right: 0px;
          color: #D5A970;
          padding-left: 6px;
          font-size: 20px;
          }
        a:hover {color: #D5A970;}
      }
      &-item.catalog:hover {
        cursor: pointer;
        color: #D5A970;
        i {color: #171717;}
      }
    }
    &__consumer {
      display: flex;
      -webkit-justify-content: flex-end;
                justify-content: flex-end;
      &-basket {
        width: 100%;
        display: flex;
        -webkit-justify-content: flex-end;
                justify-content: flex-end;
        padding: 0 20px;
        
        .counterItem {
          margin: 0 5px;
          padding: 1px 3px;
          background-color: #D5A970;
          color: #fff;
          font-weight: bold;
          font-size: 13px;
          @include border-radius(50%);
        }

        a {
          color: #D5A970;
          margin-right: 3px;
        }
        a:hover .counterItem {background-color: #171717};
        .cart {font-size: 20px;}
        .counter {
          font-weight: bold;
          font-size: 14px;
          .counterPrice {font-size: 16px;}
        }
      }

      &-cabinet {
        position: relative;
        background: url("../img/user-gold.png") no-repeat left center;
        padding-left: 25px;
        font-size: 16px;
        font-weight: bold;
        a {color: #171717;}
        a:hover {color: #D5A970};
        
        .check {
          position: absolute;
          font-size: 7px;
          bottom: 2px;
          left: 12px;
          color: #FFF;
        }
      }
    }
  }
  .container-fluide {
    position: relative;
    
  /* Слайдер */
    .slider {
      left: 0;
      top: 48px;
      max-height: 330px;
    }

    .slick-next::after, .slick-prev::before {
      position: absolute;
      content: "";
      cursor: pointer;
      width: 20px;
      height: 30px;
      top: 50%;
      margin-top: -15px;
    }

    .slick-next::after {
      right: 5px;
      background: url('../img/next-white.png') no-repeat center center;
    }

    .slick-next:hover:after {background: url('../img/next-gold.png') no-repeat center center;}

    .slick-prev::before {
      right: 9px;
      background: url('../img/prev-white.png') no-repeat center center;
    }

    .slick-prev:hover:before {background: url('../img/prev-gold.png') no-repeat center center;}
  }

  .slick-next, .slick-prev {
    position: absolute;
    border: 0;
    background: none;
    background-color: #D5A970;
    font-size: 45px;
    top: 50%;
    margin-top: -65px;
    outline: none !important;
    z-index: 20;
    cursor: pointer;
    padding: 65px 18px;
    opacity: 0.7;
    &:hover {background-color: #FFF;}
  }

  .slick-next {right: 0px;}

  .slick-prev {left: 0;}

  .slick-dots {
    margin: 0 auto;
    display: flex;
    justify-content: center;
  }

  .slick-dots li button {
    border: 0;
    background: none;
    width: 17px;
    height: 17px;
    background-color: #eedcc4;
    font-size: 0;
    margin: 0 10px;
    @include border-radius(50%);
  }

  .slick-dots li.slick-active button {background-color: #cb944c;}

  /* Catalog */
  .catalog, .subcatalog {
    padding-right: 25px;
    position: relative;
  }

  .catalog__list {
    position: absolute;
    visibility: hidden;
    width: 170%;
    z-index: 30;
    margin-top: 25px;
    background-color: #FFF;
    border: 1px solid rgba(102, 102, 102, 0.2);
    @include box-shadow(2px, 2px, 3px, rgba(102, 102, 102, 0.4));
    @include border-radius(4px);
    font-size: 19px;
    
    .dropleftArrow {
      position: absolute;
      top: 15px;
      right: 20px;
      font-size: 22px;
    }
  }

  .catalog__list::after {
    content: "";
    position: absolute;
    botoom: 20px;
    height: 25px;
    width: 100%;
    top: 0;
    margin-top: -25px;
    left: 0;
    z-index: 33;
    background-color: none;
  }

  .catalog__item {
    position: relative;
    background: url('../img/circle.png') no-repeat 20px center;
    font-weight: normal;
    text-transform: none;
    color: #e2c39a;
    a {color: #666;}
    .catalog__item-sub {color: #666;}
    a:hover {color: #666;}
    padding: 12px 0px 12px 40px;
  }

  .catalog:hover .catalog__list, .subcatalog:hover .subcatalog__list {
    position: absolute;
    visibility: visible;
  }

  .head__menu-item > ul > li:hover{
    background-color: rgba(182, 182, 182, 0.3);
    .dropleftArrow {color: #CB944C;}
  }

  .subcatalog__item {
    position: relative;
    background: url('../img/circle.png') no-repeat 20px center;
    font-weight: normal;
    text-transform: none;
    color: #e2c39a;
    a {color: #666;}
    .catalog__item-sub {color: #666;}
    a:hover {color: #666;}
    padding: 12px 0px 12px 40px;
  }

  .subcatalog__list {
    position: absolute;
    visibility: hidden;
    width: 100%;
    left: 340px;
    top: -32px;
    z-index: 0;
    margin-top: 25px;
    background-color: #FFF;
    border: 1px solid rgba(102, 102, 102, 0.2);
    @include box-shadow(2px, 2px, 3px, rgba(102, 102, 102, 0.4));
    @include border-radius(4px);
    font-size: 19px;
  }

  .subcatalog__list::after {
    content: "";
    position: absolute;
    left: 0px;
    width: 100%;
    height: 100%;
    top: 0;
    margin-left: -25px;
    z-index: -1;
    background: none;
  }

  .head__menu-item ul li > ul > li:hover{
    background-color: rgba(182, 182, 182, 0.3);
    .dropleftArrow {color: #CB944C;}
  }

  .subcatalog__title {
    padding: 15px 20px;
    font-size: 20px;
    font-weight: bold;
    text-transform: uppercase;
  }
}

