// humburger
.menuToggle__humburger {
  width: 16px;
  height: 16px;
  position: relative;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: .5s ease-in-out;
  -moz-transition: .5s ease-in-out;
  -o-transition: .5s ease-in-out;
  transition: .5s ease-in-out;
  cursor: pointer;
  span {
    display: block;
    position: absolute;
    height: 2px;
    width: 8px;
    background: #f0f1f6;
    opacity: 1;
    -webkit-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
    -webkit-transition: .25s ease-in-out;
    -moz-transition: .25s ease-in-out;
    -o-transition: .25s ease-in-out;
    transition: .25s ease-in-out;
  }
}

.menuToggle__humburger span:nth-child(even) {
  left: 50%;
}

.menuToggle__humburger span:nth-child(odd) {
  left: 0px;
}

.menuToggle__humburger span:nth-child(1), 
.menuToggle__humburger span:nth-child(2) {top: 0px;}

.menuToggle__humburger span:nth-child(3), 
.menuToggle__humburger span:nth-child(4) {top: 4px;}

.menuToggle__humburger span:nth-child(5), 
.menuToggle__humburger span:nth-child(6) {top: 8px;}

.menuToggle__humburger.open span:nth-child(1),
.menuToggle__humburger.open span:nth-child(6) {
  -webkit-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  -o-transform: rotate(45deg);
  transform: rotate(45deg);
}

.menuToggle__humburger.open span:nth-child(2),
.menuToggle__humburger.open span:nth-child(5) {
  -webkit-transform: rotate(-45deg);
  -moz-transform: rotate(-45deg);
  -o-transform: rotate(-45deg);
  transform: rotate(-45deg);
}

.menuToggle__humburger.open span:nth-child(1) {
  left: 5px;
  top: 5px;
}

.menuToggle__humburger.open span:nth-child(2) {
  left: calc(50% - 5px);
  top: 5px;
}

.menuToggle__humburger.open span:nth-child(3) {
  left: -50%;
  opacity: 0;
}

.menuToggle__humburger.open span:nth-child(4) {
  left: 100%;
  opacity: 0;
}

.menuToggle__humburger.open span:nth-child(5) {
  left: 5px;
  top: 3px;
}

.menuToggle__humburger.open span:nth-child(6) {
  left: calc(50% - 5px);
  top: 3px;
}




